// global
const body = document.body
// navbar
const navbars = document.querySelectorAll('.navbar')
for (const navbar of navbars) {
   if (navbar) {
      const burger = navbar.querySelector('.burger')
      const menu = navbar.querySelector('.nav__menu')
      burger.addEventListener('click', () => {
         if (!menu.classList.contains('active')) {
            menu.classList.add('active')
            burger.classList.add('active')
            body.classList.add('locked')
         } else {
            menu.classList.remove('active')
            burger.classList.remove('active')
            body.classList.remove('locked')
         }
      })
   }
}
// navbar top slider
const navbarSliders = document.querySelectorAll('.navbar__scroll')
for (const navbarSlider of navbarSliders) {
   if (navbarSlider) {
      new Swiper(navbarSlider, {
         direction: 'horizontal',
         loop: !0,
         slidesPerView: "auto",
         spaceBetween: 12,
         freeMode: true,
         speed: 30000,
         freeModeMomentum: false,
         autoplay: {
            delay: 1,
            disableOnInteraction: false,
         },
      });
   }
}
// missing chart
const charts = document.querySelectorAll('.chart')
for (const chart of charts) {
   if (chart) {
      const chartMob = document.querySelector('.missing__chart-mob')
      const plugin = {
         id: 'customCanvasBackgroundColor',
         beforeDraw: (chart, args, options) => {
            const { ctx } = chart;
            ctx.save();
            ctx.globalCompositeOperation = 'destination-over';
            ctx.fillStyle = options.color || '#99ffff';
            ctx.fillRect(0, 0, chart.width, chart.height);
            ctx.restore();
         }
      };
      new Chart(chart, {
         type: 'bar',
         data: {
            labels: ['', '2018', '', '2020', '', '2022', '', '', '2024', '', '2026'],
            datasets: [{
               label: false,
               data: [13.63, 20.21, 22.02, 24.57, 29.84, 36.41, 44.28, 51.01, 57.20, 63.97, 71.21],
               borderWidth: 1,
               borderColor: '#FEC200',
               backgroundColor: '#FEC200',
               borderRadius: 11,
            }]
         },
         plugins: [plugin],
         plugins: [ChartDataLabels],
         options: {
            scales: {
               y: {
                  beginAtZero: true,
                  ticks: {
                     color: '#ffffff',
                     font: {
                        size: 16,
                        family: "Poppins",
                        weight: 400,
                        lineHeight: 1.88,
                     }
                  },
               },
               x: {
                  ticks: {
                     color: '#ffffff',
                     font: {
                        size: 16,
                        family: "Poppins",
                        weight: 400,
                        lineHeight: 1.88,
                     }
                  },
               },
            },
            plugins: {
               tooltip: {
                  enabled: false,
               },
               customCanvasBackgroundColor: {
                  color: '#000000',
               },
               legend: {
                  display: false,
               },
               datalabels: {
                  color: '#ffffff',
                  labels: {
                     value: {},
                  },
                  font: {
                     size: 19,
                     family: "Poppins",
                     weight: 400,
                     lineHeight: .94,
                  },
                  anchor: 'end',
                  align: 'top',
               }
            }
         },
      });
      new Chart(chartMob, {
         type: 'bar',
         data: {
            labels: ['2018', '2020', '2022', '2024', '2026'],
            datasets: [{
               label: false,
               data: [13.63, 20.21, 36.41, 57.20, 71.21],
               borderWidth: 1,
               borderColor: '#FEC200',
               backgroundColor: '#FEC200',
               borderRadius: 5,
            }]
         },
         plugins: [plugin],
         plugins: [ChartDataLabels],
         options: {
            scales: {
               y: {
                  beginAtZero: true,
                  ticks: {
                     color: '#ffffff',
                     font: {
                        size: 10,
                        family: "Poppins",
                        weight: 400,
                        lineHeight: 1.88,
                     }
                  },
               },
               x: {
                  ticks: {
                     color: '#ffffff',
                     font: {
                        size: 10,
                        family: "Poppins",
                        weight: 400,
                        lineHeight: 1.88,
                     }
                  },
               },
            },
            plugins: {
               tooltip: {
                  enabled: false,
               },
               customCanvasBackgroundColor: {
                  color: '#000000',
               },
               legend: {
                  display: false,
               },
               datalabels: {
                  color: '#ffffff',
                  labels: {
                     value: {},
                  },
                  font: {
                     size: 12,
                     family: "Poppins",
                     weight: 400,
                     lineHeight: .94,
                  },
                  anchor: 'end',
                  align: 'top',
               }
            }
         },
      });
   }
}
